<template>
    <div class="homeWrap">
        <!--头部-->
        <header>
            <span class="logo">ele.me</span>
            <router-link to="/login">
                <span v-if="isLogin">登录|注册</span>
                <router-link to="/profile" v-else="isLogin"><i class="el-icon-user"></i></router-link>
            </router-link>
        </header>
        <!--定位城市-->
        <nav>
            <div>
                <span>当前定位城市：</span>
                <span>定位不准时，请在城市列表中选择</span>
            </div>
            <router-link :to="{name:'c',params:{cityId:guessCity ? guessCity.id : 1}}">
                <span>{{guessCity ? guessCity.name : ''}}</span>
                <i class="el-icon-arrow-right"></i>
            </router-link>
        </nav>
        <!--热门城市-->
        <section class="hot_city">
            <h4 class="city_title">热门城市</h4>
            <ul>
                <li v-for="(c, index) in hotCity" :key="c.id" class="city_style" @click="chooseCity(c.id)">{{c.name}}</li>
            </ul>
        </section>
        <!--所有城市-->
        <section class="group_city">
            <ul>
                <li v-for="(city_arr, g_key, index) in groupCity" :key="g_key" class="g_li">
                    <h4 class="city_title">
                        {{g_key}}
                        <!--<span v-if="index ? false : true">（按字母排序）</span>-->
                        <span class="tip">{{index ? "" : "（按字母排序）"}}</span>
                    </h4>
                    <ul>
                        <li v-for="city in city_arr" :key="city.id" class="city_style ellipsis" @click="chooseCity(city.id)">{{city.name}}</li>
                    </ul>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Home",
        data(){
            return {
                guessCity:null,
                hotCity:[],
                groupCity:null,
                isLogin:true
            }
        },
        created(){
            if(localStorage.getItem("loginname")){
                this.isLogin = false;
            }else {
                this.isLogin = true;
            }
            this.axios.get("/v1/cities",{
                params:{type:"guess"}
            }).then(res=>{
                this.guessCity = res;
            }).catch(err=>{});

            this.axios.get("/v1/cities",{
                params:{type:"hot"}
            }).then(res=>{
                this.hotCity = res;
            }).catch(err=>{});

            this.axios.get("/v1/cities",{
                params:{type:"group"}
            }).then(res=>{
                this.groupCity = res;
                this.handleGroupCity(res);
            }).catch(err=>{});
        },
        methods:{
            handleGroupCity(cityGroup){
                const orderCity = {};
                //A-Z排序
                Object.keys(cityGroup).sort().forEach(key=>{
                    orderCity[key] = cityGroup[key];
                });
                this.groupCity = orderCity;
            },
            chooseCity(cId){
                this.$router.push({name:'c',params:{cityId:cId}});
            }
        }
    }
</script>

<style>
    .el-icon-user {
        font-size: .8rem;
        color: #fff;
    }
</style>
<style scoped>
    .logo {
        left: .4rem;
        font-weight: 400;
        font-size: .7rem;
        color: #fff;
        width: 2.3rem;
        height: .7rem;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    header>a {
        right: .55rem;
        font-size: .65rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    nav {
        width: 100%;
        padding-top: 2.35rem;
        background-color: #fff;
        margin-bottom: .4rem;
    }
    nav>div {
        display: flex;
        justify-content: space-between;
        padding: 0 .45rem;
        line-height: 1.45rem;
    }
    nav>div>span:nth-child(1) {
        font-size: .55rem;
        color: #666;
    }
    nav>div>span:nth-child(2) {
        font-weight: 900;
        font-size: .475rem;
        color: #9f9f9f;
    }
    nav>a {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 1.8rem;
        padding: 0 .45rem;
        border-top: 1px solid #e4e4e4;
        border-bottom: 2px solid #e4e4e4;
        font-size: .75rem;
        line-height: 1.8rem;
        font-family: Microsoft YaHei;
    }
    nav>a>span {
        color: #3190e8;
    }
    nav>a>i {
        color: #999;
    }
    .hot_city {
        background-color: #fff;
        margin-bottom: .4rem;
    }
    .city_title {
        color: #666;
        font-weight: 400;
        text-indent: .45rem;
        border-top: 2px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        font: .55rem/1.45rem Helvetica Neue;
    }
    .hot_city>ul {
        overflow: hidden;
    }
    .city_style {
        float: left;
        text-align: center;
        color: #3190e8;
        border-bottom: .025rem solid #e4e4e4;
        border-right: .025rem solid #e4e4e4;
        box-sizing: border-box;
        width: 25%;
        height: 1.75rem;
        font: .6rem/1.75rem Microsoft YaHei;
    }
    .g_li {
        margin-bottom: .4rem;
        background-color: #fff;
        border-bottom: 1px solid #e4e4e4;
    }
    .g_li>ul {
        overflow: hidden;
    }
    .tip {
        font-size: .475rem;
        color: #999;
    }
    .ellipsis {
        color: #666;
    }
    .usericon {
        font-size: .8rem;
    }
</style>